.Button {
    width: 180px;
    margin: 0 auto;
    padding: 10px 16px;
    border-radius: 50px;
    background-color: #61aacb;
    transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
    color: #fff;
    border: 0;
    text-decoration: none;
    cursor: pointer;
    font-family: inherit;
    font-size: 18px;
    line-height: 24px;
    font-style: normal;
    font-weight: 500;
    box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2),
        0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
};

.Button:hover {
    background-color: #4d87a1;
};

.Button:focus {
    background-color: #295265;
}
